import {InjectionToken} from '@angular/core';

export interface GoogleLoginConfig {
  authCodeUrl: string;
  userTokenServerUrl: string;
  sendOtpUrl?:string,
  validateOtpUrl?:string;
  validateUserUrl?: string;
  auth: {
    google: {
        client_id: string,
        redirect_uri?: string,
        scope?: string
    },
    server: {
        client_id: string,
        client_secret: string
    }
  };
  user:{
    userCookie:string,
    authenticateUserUrl:string,
    loginStateCookie:string
  }
  logout:{
    logoutUrl:string
  }
}

export const GOOGLE_LOGIN_CONFIG = new InjectionToken<GoogleLoginConfig>('chalo-google-login.config');
