import { Component, OnInit, ViewEncapsulation, Output, EventEmitter, Input } from '@angular/core';
import { GoogleLoginResponse, StatusCode } from "./models/chalo-google-login-response";
import { GoogleLoginService } from "./chalo-google-hybrid-login.service";

@Component({
  selector: 'chalo-google-hybrid-login',
  templateUrl: './chalo-google-hybrid-login.component.html',
  encapsulation: ViewEncapsulation.None,
  styleUrls: ['./chalo-google-hybrid-login.component.css']
})
export class ChaloGoogleHybridLoginComponent implements OnInit {
  @Output() onLoginResponse: EventEmitter<GoogleLoginResponse> = new EventEmitter<GoogleLoginResponse>();
  @Input() hasMobileLogin = false;
  @Input() hasUserLogin = false;
  showGoogleLogin = true;
  loginType = -1;
  
  constructor(private loginService: GoogleLoginService<any>) {

  }

  ngOnInit() {
    if (this.loginService.isUserAuthenticated()) {
      this.loginService.getToken().subscribe((data) => {
        this.loginService.setGoogleLoginResponse({ code: StatusCode.Success, data })
      }, (err) => {
        this.loginService.setGoogleLoginResponse({ code: StatusCode.Failure, data: err })

      });
    } else {
      console.log("cookie not available")
    }
  }

  getGoogleLogin() {
    this.loginService.getGoogleLogin().subscribe((data) => {
      this.onLoginResponse.emit({ code: StatusCode.Success, data });
    }, (err) => {
      this.onLoginResponse.emit({ code: StatusCode.Failure, data: err });
    });
  }

  showLoginType(type) {
    this.loginType = type;
    this.showGoogleLogin=false;
  }

  onToggleOption(show) {
    this.showGoogleLogin = show;
  }

  onBack(){
    this.showGoogleLogin=true
    this.loginType = -1;
  }


  signInResponse(e) {
    this.onLoginResponse.emit(e);
  }
}
